export default {
  data: function data() {
    return {
      time: 0
    };
  },
  mounted: function mounted() {
    var _this = this;

    if (this.$route.query.shopUrl) {
      this.time = setInterval(function () {
        _this.getLoginStatus(_this.$route.query.shopUrl);
      }, 2000);
    } else {
      this.$router.push({
        path: "/login"
      });
    }
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.time);
  },
  methods: {
    getLoginStatus: function getLoginStatus(shopUrl) {
      var _this2 = this;

      this.$apiCall("api.ShopifyAccount.getShopAuthStatus", {
        shopUrl: shopUrl
      }, function (r) {
        if (r.ErrorCode == "9999") {
          clearInterval(_this2.time);

          if (r.Data.Results.status && r.Data.Results.status == 1) {
            localStorage.setItem("c_apiUserId", r.Data.Results.userInfo.id);
            localStorage.setItem("c_apiUserToken", r.Data.Results.userInfo.apiUserToken);
            localStorage.setItem("c_ourMallUserInfo", JSON.stringify(r.Data.Results.userInfo));

            _this2.$store.commit("setUserInfo", r.Data.Results.userInfo);

            _this2.$router.push({
              name: "dashboard"
            });
          } else {
            _this2.$elementMessage(r.Message, "error");

            setTimeout(function () {
              window.location.href = '/';
            }, 3000);
          }
        } else {
          _this2.$elementMessage(r.Message, "error");

          setTimeout(function () {
            window.location.href = '/';
          }, 3000);
        }
      });
    }
  }
};